import {updateucenter,myinfo,getConfig,uploadimg,weixinuploadimg} from '@/api/ucenter.js'
export default {
    created () {
        // 
        let href = window.location.href.split("#")
        let url = href[0]
        let data = {
            url: url
        };
        getConfig(data).then(res => {
            // 
            // console.log(res.data);
            wx.config({
			debug: false,
			appId: res.data.appId,
			timestamp: res.data.timestamp,
			signature: res.data.signature,
			nonceStr: res.data.nonceStr,
			jsApiList: [
				'checkJsApi',
				'getLocation',
				'chooseImage',
                'uploadImage',
                'downloadImage'
			] // JSJS2
        });
        });

        
    },
    data () {
        return {
            mediaId: '',
            imgsrc: ''
        }
    },
    methods: {
        getready () {
            let _this = this
            wx.ready(function(){
                _this.getImg()
            });
            // 
            wx.error(function(res){
                // console.log(res)
            });
        },
        //
        getImg(){
            let _this = this
            wx.chooseImage({
                count: 1, // 9
                sizeType: ['original', 'compressed'], // 
                sourceType: ['album', 'camera'], // 
                success: function (res) {
                    var localIds = res.localIds[0].toString() // IDlocalIdimgsrc
                    _this.uploadImg(localIds)
                }
            });

        },
        // 
        uploadImg(val) {
            let _this = this 
            wx.uploadImage({  
                localId: val, // IDchooseImage  
                isShowProgressTips: 1, // 1  
                success: function (res) {  
                    _this.mediaId = res.serverId; // ID  
                    // _this.imgsrc = localIds
 
                    // 
                    let fdata = {
                        SERVER_ID:  _this.mediaId 
                    }
                    weixinuploadimg(fdata).then(res => {
                        if (res.data.result == 0) {
                            _this.imgsrc = res.data.datas.headimgurl
                            // url
                            _this.$emit('outimg', _this.imgsrc)
                        }
                    })
                   

                },  
                fail: function (error) {  
                    picPath = '';  
                    localIds = '';  
                    alert(Json.stringify(error));  
  
                }  
  
            });   
        },
    }
}



